import React, { useState, useCallback, useMemo, useRef } from 'react';
import ReactFlow, {
  ReactFlowProvider,
  useNodesState,
  useEdgesState,
  addEdge,
  useReactFlow,
  MiniMap,
} from 'reactflow';
import 'reactflow/dist/style.css';

import * as S from './style'

import ForwardToInboxIcon from '@mui/icons-material/ForwardToInbox';
import RuleIcon from '@mui/icons-material/Rule';
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import ButtonPadrao from '../Button';
import SaveIcon from '@mui/icons-material/Save';
import RestoreIcon from '@mui/icons-material/Restore';
import SideBarDraggableComponent from './components/SideBar';
import axios from 'axios'

import TextUpdater from './components/TextUpdater';
import ValidateMessage from './components/ValidateMessage';
import ValidateMessageWithSingleResponse from './components/ValidateMessageWithSingleResponse';
import CustomEdge from './components/DefaultCustomEdge';

import HTMLReactParser from 'html-react-parser';

const flowKey = 'example-flow';

const getNodeId = () => `${+new Date()}`;

const initialNodes = [
  { id: '1', data: { label: 'Ao receber uma mensagem' }, type: 'input', position: { x: 350, y: 50 } }
];

const permiteMultiplosFilhosCasoValidateMessageMiddleware = (params, eds) => {
  let validaConexao = true
  let tipoDoNodeSource = params.source.split('_')[0]
  let tipoDoNodeTarget = params.target.split('_')[0]

  eds.map(value => {
    let tipoDoNodeSourceListaAnterior = value.source.split('_')[0]
    let tipoDoNodeTargetListaAnterior = value.target.split('_')[0]

    if (value.source === '1' && params.source === '1') {
      validaConexao = false
      console.log('somente permitido uma conexo com input - caso 1')
    }
    if (params.source === value.source) {
      let verificaFilhosDiferentes = ((tipoDoNodeTarget === tipoDoNodeTargetListaAnterior) && (tipoDoNodeTarget === 'validateMessage'))

      if (!verificaFilhosDiferentes) {
        validaConexao = false
        console.log('no  permitido realizar a conexo de 2 tipos filho diferentes - caso 2')
      }
    }
  })
  return validaConexao
}

const SaveRestore = () => {
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [rfInstance, setRfInstance] = useState(null);
  const [objectEdit, setObjectEdit] = useState({});
  const { setViewport } = useReactFlow();
  const reactFlowWrapper = useRef(null);

  const nodeTypes = useMemo(() => ({ textUpdater: TextUpdater, validateMessage: ValidateMessage, validateMessageWithSingleResponse: ValidateMessageWithSingleResponse }), []);
  const edgeTypes = useMemo(() => ({ custom: CustomEdge }), []);

  const onConnect = useCallback((params) => setEdges((eds) => {
    let validaConexao = permiteMultiplosFilhosCasoValidateMessageMiddleware(params, eds)
    if (validaConexao) {
      return addEdge({ ...params, type: 'custom', markerEnd: { type: 'arrow', width: 25, height: 50 } }, eds)
    } else {
      return eds
    }
  }
  ), []);

  const onSave = useCallback(() => {
    if (rfInstance) {
      const flow = rfInstance.toObject();
      localStorage.setItem(flowKey, JSON.stringify(flow));
    }
  }, [rfInstance]);

  const onRestore = useCallback(() => {
    const restoreFlow = async () => {
      const flow = JSON.parse(localStorage.getItem(flowKey));
      if (flow) {
        const { x = 0, y = 0, zoom = 1 } = flow.viewport;
        setNodes(flow.nodes || []);
        setEdges(flow.edges || []);
        setViewport({ x, y, zoom });
      }
    };

    restoreFlow();
  }, [setNodes, setViewport]);

  const onNodeClick = (event, object) => {
    setObjectEdit(object);
  };

  const onPaneClick = () => {
    setObjectEdit({});
  };

  const onSubmit = async () => {
    const flow = JSON.parse(localStorage.getItem(flowKey));
    console.log(flow)
    try {
      const response = await axios.post('http://localhost:8080/teste', flow)
        .then(res => {
          console.log(res)
        })
    } catch (erro) {
      console.log(erro.response.message)
    }
  };

  const onDragOver = useCallback((event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop = useCallback(
    (event) => {
      event.preventDefault();

      const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      const type = event.dataTransfer.getData('application/reactflow');
      
      // check if the dropped element is valid
      if (typeof type === 'undefined' || !type) {
        return;
      }

      const position = rfInstance.project({
        x: event.clientX - reactFlowBounds.left,
        y: event.clientY - reactFlowBounds.top,
      });

      let geraId = getNodeId()

      if(type === 'validateMessageWithSingleResponse') {
        const newNode = {
          id: `${type}_${geraId}`,
          type,
          position,
          data: { label: `${type} node`, messageToValidate: [''] },
        };  
        setNodes((nds) => nds.concat(newNode));
      } else {
        const newNode = {
          id: `${type}_${geraId}`,
          type,
          position,
          data: { label: `${type} node` },
        };
        setNodes((nds) => nds.concat(newNode));
      }
    },
    [rfInstance]
  );

  const handleSideBar = (e) => {
    console.log('EEEEEEEEEEEEEE', e)

    const incluirPalavraNaResposta = (parametro, objetoDadosNode, palavra) => {
      const index = 3
      let resposta = e.target.value
      let arrResposta = []
      for(let x = 0; x < resposta.length; x++) {
        arrResposta.push(resposta[x])
      }
      arrResposta.splice(index, 0, palavra)
      const respostaFinal = arrResposta.join('')
      console.log(respostaFinal)
      return {
        ...objectEdit,
        data: { ...objetoDadosNode.data, [parametro]: respostaFinal }
      }
    }

    const pegarObjetoEdicao = (parametro, objetoDadosNode) => {
      return {
        ...objectEdit,
          data: { ...objetoDadosNode.data, [parametro]: e.target.value } 
      }
    }



    const alterarItemListaDeCamposDoObjeto = (parametro, objetoDadosNode, indiceCampo) => {
      const resposta = e.target.value
     
      let arrCondicionais = objetoDadosNode.data.messageToValidate
      arrCondicionais[indiceCampo] = resposta
      return {
        ...objectEdit,
          data: { ...objetoDadosNode.data, [parametro]: arrCondicionais }
      }
    }

    const pegarNewNode = (parametro) => {
      const newNode = nodes.map((item) => {
          if (item.id === objectEdit.id) {
            const editarObjeto = pegarObjetoEdicao(parametro, objectEdit)
            return editarObjeto
          }
          return item
        })
      return newNode
    }

    const excluirActionMessageDoObjeto = (item) => {
      const asArray = Object.entries(item.data)
      const filtered = asArray.filter(([key, value]) => key != 'actionMessage')
      const objectData = { data: Object.fromEntries(filtered) }
      return objectData
    }

    const actionTypes = {
      Message(item) {
        const resposta = pegarObjetoEdicao('action' , objectEdit)
        return resposta
      },
      Tuya(item) {
        const transformaObj = excluirActionMessageDoObjeto(item)
        const resposta = pegarObjetoEdicao('action', transformaObj)
        return resposta
      },
      CloseSession(item) {
        const transformaObj = excluirActionMessageDoObjeto(item)
        const resposta = pegarObjetoEdicao('action', transformaObj)
        return resposta
      }
    }

    const validationInputHandler = {
      aoReceberMensagem() {
        setObjectEdit(pegarObjetoEdicao('messageToValidate', objectEdit))
        const newNode = pegarNewNode('messageToValidate')
        setNodes(newNode)  
      },

      selecionarAcao() {
        setObjectEdit(pegarObjetoEdicao('action', objectEdit))
        console.log(objectEdit)
        
        const newNode = nodes.map((item) => {
          if (item.id === objectEdit.id) {
            const handleActionEdit = actionTypes[e.target.value]
            const final = handleActionEdit(item)
            return final
          }
          return item
        })
        console.log(newNode)
        setNodes(newNode)
      },

      acceptedMessageResponse() {
        // const palavra = 'palavra'
        const objetoEdicao = pegarObjetoEdicao('actionMessage', objectEdit)
        // const objetoEdicaoComPalavraInclusa = incluirPalavraNaResposta('actionMessage', objetoEdicao, palavra)
        console.log('objetoEdicaoobjetoEdicaoobjetoEdicao', objetoEdicao)
        setObjectEdit(objetoEdicao)
        const newNode = nodes.map((item) => {
          if (item.id === objectEdit.id) {
            const editarObjetoRespostaComParametros = {
              ...objectEdit,
              data: { ...objectEdit.data, actionMessage: e.target.value 
              //   {
              //   comment: e.target.value
              // }
            }
            }
            const editarObjeto = pegarObjetoEdicao('actionMessage', objectEdit)
            // console.log('editarObjeto', editarObjeto)
            console.log('editarObjetoRespostaComParametroseditarObjetoRespostaComParametros', editarObjetoRespostaComParametros)
            // const respostaComPalavraInclusa = incluirPalavraNaResposta('actionMessage', editarObjeto, palavra)
            return editarObjetoRespostaComParametros
          }
          return item
        })
        // const newNode = pegarNewNode('actionMessage')
        console.log('newNode', newNode)
        setNodes(newNode)
      },

      aoReceberMensagens() {
        const indiceCampo =  e.currentTarget.getAttribute('acessarindexid')
        setObjectEdit(alterarItemListaDeCamposDoObjeto('messageToValidate', objectEdit, indiceCampo))          
        const newNode = nodes.map((item) => {
          if (item.id === objectEdit.id) {
            const editarObjeto = alterarItemListaDeCamposDoObjeto('messageToValidate', objectEdit, indiceCampo)
            return editarObjeto
          }
          return item
        })
        // console.log(newNode)
        setNodes(newNode)
      }

    }

    const tipoNode = {
      textUpdater() {
        setObjectEdit(pegarObjetoEdicao('value', objectEdit))
        const newNode = pegarNewNode('value')
        // console.log('newNode', newNode)
        setNodes(newNode)
      },

      validateMessage() {
        const editarInput = validationInputHandler[e.target.name]
        editarInput()
      },

      validateMessageWithSingleResponse() {
        console.log('EVENTO INICIAL', e)
        if(e.target.name) {
          const editarInput = validationInputHandler[e.target.name]
          editarInput()
        }
      }
    }
    const handleNodeSelected = tipoNode[objectEdit.type]
    handleNodeSelected()
  }

  const handleClickSideBar = (e) => {
    
    const sideBarButtons = {
      deploy() {
        return onSubmit()
      },

      addValidationMessageSingleResponseCase() {
        const tipoMessageToValidate = typeof objectEdit.data.messageToValidate
        if (tipoMessageToValidate === 'object') {
          let arrMessageToValidate = objectEdit.data.messageToValidate
          arrMessageToValidate.push('')
          // console.log(arrMessageToValidate)
          const listaIds = nodes.map(value => value.id)
          const index = listaIds.indexOf(objectEdit.id)
          let listaNodes = nodes
          const nodeAnterior = nodes[index]
          const listaSemNodeAnterior = listaNodes.splice(index, 1)
          const modifiedNode = [{
            ...nodeAnterior,
            data: { ...nodeAnterior.data, messageToValidate: arrMessageToValidate }
          }]
          const nodeFinal = listaSemNodeAnterior.concat(modifiedNode)
          
          setNodes(nodeFinal)
        }
      },

      removeValidationMessageSingleResponseCase() {
        const indexArr = Number(e.currentTarget.getAttribute('acessarindexid'))
        let arrMessageToValidate = objectEdit.data.messageToValidate
        arrMessageToValidate.splice(indexArr, 1)
        const listaIds = nodes.map(value => value.id)
        const indexNode = listaIds.indexOf(objectEdit.id)
        let listaNodes = nodes
        const nodeAnterior = nodes[indexNode]
        const listaSemNodeAnterior = listaNodes.splice(indexNode, 1)
        const modifiedNode = [{
          ...nodeAnterior,
          data: { ...nodeAnterior.data, messageToValidate: arrMessageToValidate }
        }]
        const nodeFinal = listaSemNodeAnterior.concat(modifiedNode)
        const [objeto] = modifiedNode
        setObjectEdit(objeto)
        setNodes(nodeFinal)
      },

    }
    if(e.currentTarget.name) {
      const clickedButton = sideBarButtons[e.currentTarget.name]
      clickedButton()
    }

  }
  
  return (
    <S.Container>
      {/* <div>{JSON.stringify(nodes)}</div> */}
      <S.SideBar>
        <S.ContainerInputs>
          <SideBarDraggableComponent
            objectEdit={objectEdit}
            onChange={(e) => handleSideBar(e)}
            onClick={(e) => handleClickSideBar(e)} />
        </S.ContainerInputs>
      </S.SideBar>
      <S.Container ref={reactFlowWrapper}>
        <ReactFlow
          nodes={nodes}
          edges={edges}
          nodeTypes={nodeTypes}
          edgeTypes={edgeTypes}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onNodeClick={onNodeClick}
          onPaneClick={onPaneClick}
          onConnect={onConnect}
          // connectionLineType={ConnectionLineType.SmoothStep}
          deleteKeyCode={'Delete'}
          onInit={setRfInstance}
          onDrop={onDrop}
          onDragOver={onDragOver}
        >
          <MiniMap />
        </ReactFlow>
      </S.Container>
      
      <S.ContainerSaveRestore>
        <Tooltip title="Salvar">
          <IconButton>
            <SaveIcon onClick={onSave} />
          </IconButton>
        </Tooltip>
        <Tooltip title="Restaurar">
          <IconButton>
            <RestoreIcon onClick={onRestore} />
          </IconButton>
        </Tooltip>
      </S.ContainerSaveRestore>
    </S.Container>
  );
};

export default () => (
  <ReactFlowProvider>
    <SaveRestore />
  </ReactFlowProvider>
);