import React from 'react';
import { getBezierPath, getSmoothStepPath } from 'reactflow';

export default function CustomEdge({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  style = {},
  data,
  markerEnd,
}) {
  const parametros = {
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
    borderRadius: 0
  }

  function checarPosicao(source_Y, target_Y) {
    if(source_Y < target_Y) {
      const [edgePath] = getBezierPath(parametros);
      return edgePath
    } else {
      const [edgePath] = getSmoothStepPath(parametros);
      return edgePath
    }
  }
  const edgePathFinal = checarPosicao(sourceY, targetY)
  return (
    <>
      <path
        id={id}
        style={style}
        className="react-flow__edge-path"
        d={edgePathFinal}
        markerEnd={markerEnd}
      />
    </>
  );
}
