import React, { useState } from 'react';

import * as S from './style'

import ForwardToInboxIcon from '@mui/icons-material/ForwardToInbox';
import MergeIcon from '@mui/icons-material/Merge';
import CallSplitIcon from '@mui/icons-material/CallSplit';

import Autocomplete from '@mui/material/Autocomplete';
import ButtonPadrao from '../../../Button';
import TextField from '@mui/material/TextField';
import { MenuItem } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import Select from '@mui/material/Select';
import Tooltip from '@mui/material/Tooltip';
import DataObjectIcon from '@mui/icons-material/DataObject';

import MenuComponent from '../MenuComponent';

import InputTextComMention from '../MentionInput';
import { MentionsInput, Mention } from 'react-mentions'

const onDragStart = (event, nodeType) => {
  event.dataTransfer.setData('application/reactflow', nodeType);
  event.dataTransfer.effectAllowed = 'move';
};

export default function SideBarDraggableComponent(props) {
  const [state, setState] = useState({ comment: "" })
  const top100Films = [
    { label: 'The Shawshank Redemption', year: 1994 },
    { label: 'The Godfather', year: 1972 }
  ]
  const users = [
    {
      id: '@walter',
      display: 'Walter White',
    },
    {
      id: 'pipilu',
      display: '',
    },
    {
      id: 'luxixi',
      display: '',
    },
    {
      id: 'satoshi1',
      display: '',
    }
  ]

  function onAdd(id, display) {
    // Do something here

    console.log('id, display', id, display);
    console.log("Comment inside onAdd: ");
  }
  return (
    <S.Container>
      <S.Container>
        <S.DragInputItem onDragStart={(event) => onDragStart(event, 'textUpdater')} draggable>
          <ForwardToInboxIcon />
          <S.DragInputTitle>
            Enviar mensagem
          </S.DragInputTitle>
        </S.DragInputItem>
        <S.DragInputItem onDragStart={(event) => onDragStart(event, 'validateMessage')} draggable>
          <CallSplitIcon />
          <S.DragInputTitle>
            Validar mensagem recebida com mltiplas respostas
          </S.DragInputTitle>
        </S.DragInputItem>
        <S.DragInputItem onDragStart={(event) => onDragStart(event, 'validateMessageWithSingleResponse')} draggable>
          <MergeIcon />
          <S.DragInputTitle>
            Validar mensagem recebida com resposta nica
          </S.DragInputTitle>
        </S.DragInputItem>
        <S.ContainerEdicao>
          {
            props.objectEdit.type === "textUpdater" && (
              <S.Container>
                <S.ItemTitle>Em seguida, enviar a mensagem:</S.ItemTitle>
                <TextField
                  required
                  id="outlined-required"
                  label="Required"
                  fullWidth
                  multiline
                  maxRows={6}
                  type='text'
                  name="value"
                  value={props.objectEdit.data.value}
                  onChange={props.onChange} />
              </S.Container>
            )
          }
          {
            (props.objectEdit.type === "validateMessage") && (
              <S.Container>
                <S.ItemTitle>Ao receber a mensagem:</S.ItemTitle>
                
                <TextField
                  required
                  id="outlined-required"
                  label="Required"
                  fullWidth
                  multiline
                  maxRows={2}
                  type='text'
                  value={props.objectEdit.data.messageToValidate}
                  name="aoReceberMensagem"
                  onChange={props.onChange} />
                <S.ItemTitle>Enviar resposta caso a condio acima seja atendida:</S.ItemTitle>
                <Select
                  value={props.objectEdit.data.action}
                  label="Ao"
                  name="selecionarAcao"
                  onChange={props.onChange}>
                  <MenuItem value={'Tuya'}>Enviar senha dinmica Tuya</MenuItem>
                  <MenuItem value={'Message'}>Enviar mensagem</MenuItem>
                  <MenuItem value={'CloseSession'}>Encerrar conversa</MenuItem>
                </Select>
              </S.Container>
            )
          }
          {
            (props.objectEdit.type === "validateMessageWithSingleResponse") && (
              <S.Container>
                <S.ItemTitle>Ao receber as mensagens:</S.ItemTitle>
                <S.ContainerTextInputAndAddButton>
                  {
                    // console.log('props.objectEdit.data.messageToValidate', props.objectEdit.data.messageToValidate)
                    (props.objectEdit.data.messageToValidate) && props.objectEdit.data.messageToValidate.map((inputResposta, i) => {
                      return (
                        <S.ContainerListInputs>
                          <S.ContainerTextField>
                            <TextField
                              required
                              id="outlined-required"
                              label="Required"
                              fullWidth
                              multiline
                              maxRows={2}
                              inputProps={{acessarindexid: i}}
                              type='text'
                              value={inputResposta}
                              name={`aoReceberMensagens`}
                              onChange={props.onChange} />
                          </S.ContainerTextField>
                          <>
                            {
                              props.objectEdit.data.messageToValidate.length !== 1 &&
                              (
                                <IconButton 
                                  name="removeValidationMessageSingleResponseCase"
                                  acessarindexid={i}       
                                  onClick={props.onClick}
                                >
                                  <DeleteIcon 
                                    fontSize={'small'} 
                                  />
                                </IconButton>
                              )
                            }
                            {
                              props.objectEdit.data.messageToValidate.length - 1 === i && (
                                <IconButton name="addValidationMessageSingleResponseCase" onClick={props.onClick} >
                                  <AddIcon fontSize={'small'} />
                                </IconButton>
                              )
                            }
                          </>
                        </S.ContainerListInputs>
                      )
                    })
                  }
                </S.ContainerTextInputAndAddButton>
                <S.ItemTitle>Enviar resposta caso uma das condies acima seja atendida:</S.ItemTitle>
                <Select
                  required
                  value={props.objectEdit.data.action}
                  name="selecionarAcao"
                  onChange={props.onChange}>
                  <MenuItem value={'Tuya'}>Enviar senha dinmica Tuya</MenuItem>
                  <MenuItem value={'Message'}>Enviar mensagem</MenuItem>
                  <MenuItem value={'CloseSession'}>Encerrar conversa</MenuItem>
                </Select>
              </S.Container>
            )
          }
          {
            ((props.objectEdit.type === "validateMessage") || (props.objectEdit.type === "validateMessageWithSingleResponse")) && (props.objectEdit.data.action === 'Message') && (
              <S.ItemTextResponseInput>
                <TextField
                  required
                  id="outlined-required"
                  label="Required"
                  fullWidth
                  multiline
                  maxRows={2}
                  type='text'
                  value={props.objectEdit.data.messageToValidate}
                  name="acceptedMessageResponse"
                  onChange={props.onChange} />
                {/* <InputTextComMention name="acceptedMessageResponse" />                           */}
              </S.ItemTextResponseInput>
            )
          }
          {/* {
            ((props.objectEdit.type === "validateMessage") || (props.objectEdit.type === "validateMessageWithSingleResponse")) && (props.objectEdit.data.action === 'Tuya') && (
              <S.ItemTextResponseInput>
                <S.ConainerIncludeVariable>
                  <Autocomplete
                  disablePortal
                  id="combo-box-demo"
                  options={top100Films}
                  renderInput={(params) => <TextField {...params} label="Movie" />}> {`{}`}</Autocomplete>
                </S.ConainerIncludeVariable>
                <TextField
                  required
                  id="outlined-required"
                  label="Required"
                  fullWidth
                  multiline
                  maxRows={6}
                  type='text'
                  name="acceptedMessageResponse"
                  value={props.objectEdit.data.actionMessage}
                  onChange={props.onChange} />
              </S.ItemTextResponseInput>
            )
          } */}
        </S.ContainerEdicao>
      </S.Container>
      <S.ContainerButtons>
        <S.ContainerSaveRestore>
          <ButtonPadrao color="secondary" titulo={'Deploy'} name="deploy" variant="contained" onClick={props.onClick} />
        </S.ContainerSaveRestore>
      </S.ContainerButtons>
    </S.Container>
  )
}