    const handleLogOut = async (e) => {
        e.preventDefault()
        const token = localStorage.getItem('Authorization')
        const config = {
            headers: { Authorization: `Bearer ${token}` }
        };
        try {
            const response = await axios.get('http://localhost:3001/logout', config)
            .then(res => {
                if(res.status === 204) {
                    localStorage.clear()
                    navigate('/')
                } 
            }) 
            // await axios.get('http://localhost:1234/logout')
        } catch (erro) {
            // AAAAAAAAAAAAAAAAAAAAAAAAAAAAAA
            // <A><A><A>
            //     <A>A</A></A></A></A>
            console.log(erro.response.data.erro)
        }
    }
    
