import { useCallback } from 'react';
import { getConnectedEdges, Handle, Position } from 'reactflow';
import MergeIcon from '@mui/icons-material/Merge';
import * as S from './style'

// const handleStyle = { left: 10 };

export default function ValidateMessageWithSingleResponse({ type, data }) {
  return (
    <>
      <Handle type="target" position={Position.Top} />
      <S.Container>
        <S.ContainerTitleCard>
          <MergeIcon fontSize={"small"} />
          <S.Label htmlFor="text">Validar mensagem recebida:</S.Label>
        </S.ContainerTitleCard>
        <S.ContainerMessage>
          Ao receber a mensagem:
          <br />
          {data.messageToValidate}
          <br />
          Ao:
          <br />
          {data.action}
        </S.ContainerMessage>
      </S.Container>
      <Handle type="source" position={Position.Bottom} id="c" />
    </>
  );
}