import styled from 'styled-components'

export const Container = styled.div`
    display: flex;
    border: 1px solid purple;
    width: 100%;
    min-height: 550px;
    min-height: 800px;
`

export const SideBar = styled.div`
    z-index: 4;
    padding: 10px;
    width: 30%;
    min-width: 400px;
    height: 100%;
    border: 1px solid blue;
    border-radius: 5px;
    background-color: white;
`

export const ContainerInputs = styled.div`
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    padding: 5px;
    border: 1px solid yellow;
`

export const ContainerSaveRestore = styled.div`
    position: absolute;
    right: 5px;
    top: 100px;
    z-index: 4;
`
