import { useCallback } from 'react';
import { ConnectionLineType, getConnectedEdges, Handle, Position } from 'reactflow';
import CallSplitIcon from '@mui/icons-material/CallSplit';
import * as S from './style'

// const handleStyle = { left: 10 };

export default function ValidateMessage({ type, data }) {
  return (
    <>
      <Handle type="target" position={Position.Top} />
      <S.Container>
        <S.ContainerTitleCard>
          <CallSplitIcon fontSize={"small"} />
          <S.Label htmlFor="text">Validar mensagem recebida:</S.Label>
        </S.ContainerTitleCard>
        <S.ContainerMessage>
          Ao receber a mensagem:
          <br />
          {data.messageToValidate}
          <br />
          Ao:
          <br />autoatendimento
          {data.action}
        </S.ContainerMessage>
      </S.Container>
      <Handle type="source" position={Position.Bottom} />
      {/* <Handle type="source" position={Position.Bottom} id="b" /> */}
    </>
  );
}